<template>
  <div class="personal-content-body">
    <div class="content creditQueryAdd">
      <div class="content-title clear">
        <div class="left">基金权限</div>
      </div>
      <div class="account-group">
        <div class="left breadcrumb">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{name:'administrativeManageHomepageNew'}">行政管理</el-breadcrumb-item>
            <!-- <el-breadcrumb-item :to="{name:'administrativeManageHomepage'}">行政管理</el-breadcrumb-item> -->
            <el-breadcrumb-item :to="{ path: '/home/fundPermissions?page=' + page }">基金权限</el-breadcrumb-item>
            <el-breadcrumb-item>{{fundName}}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="line"></div>
        <div class="company-name">{{fundName}}</div>
        <div class="step1-box">
          <div class="step-title step1-title">基金经理设置</div>
          <div class="step1-select-box investPersonaInfoPublish">
            <el-select class=" header-right-time" v-model="projectManagerId" filterable placeholder="请选择一位人员成为投资经理">
              <el-option-group v-for="group in peoplelist" :key="group.name" :label="group.name">
                <el-option v-for="item in group.children" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-option-group>
            </el-select>
          </div>
        </div>
        <div class="projectTransfer-left-top clear">
          <div class="projectTransfer-left-left left">
            <div class="projectTransfer-left-left-title">其他拥有本基金权限的人员设置</div>
            <div class="select-tree">
              <div class="search-inputbox">
                <el-input placeholder="输入人名可进行搜索" v-model="filterText" class="header-right-time" :clearable='true'>
                  <i slot="prefix" class="el-input__icon el-icon-search "></i>
                </el-input>
              </div>
              <div class="projectTransfer-left-left-treetitle">部门</div>
              <div class='el-tree-box'>
                <el-tree :data="peoplelist" show-checkbox node-key="id" class="manegerTrees" :default-checked-keys="defaultChechedArr" :default-expanded-keys="defaultChechedArr" ref="tree" :props="defaultProps" @check='checkMethod' :filter-node-method="filterNode">
                </el-tree>
              </div>
              <!-- <div class="nodata" v-if="!nodata1"><i class="el-icon-info"></i>没有相关数据</div> -->
            </div>
          </div>
          <div class="projectTransfer-left-right left">
            <div class="projectTransfer-left-right-content-title">已选（{{otheruser.length}}）</div>
            <div class="selected-project-list">
              <div class="selected-project-item" v-for="(item,index) in otheruser" :key="index">
                <span class="icon-close" @click="deleteUser(item.user_id)"></span> {{item.name}}
              </div>
            </div>
          </div>
        </div>
        <div class="projectTransfer-left-bottom-btnbox clear">
          <div class="save-vicarious-btn btn-blue left" @click="toSetComManagerAndUser">提交</div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
</template>
<script>
import {
  getFundUser,
  setFundUser
} from '../../config/api'
import {
  dialogConfirm,
  compareJson,
  dialogConfirmHasbtn,

} from '../../config/mUtils'
export default {
  data() {
    return {
      fundId: '', //公司id
      fundName: '', //公司名字
      peoplelist: [], //人员列表
      projectManagerId: '', //投资经理设置
      otheruser: [],


      //tree控件
      filterText: '',
      // nodata1:true,
      defaultChechedArr: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      number: 0,
      page:1,
    }
  },
  created() {
    this.fundId = this.$route.query.id;
    this.fundName = decodeURIComponent(this.$route.query.name);
    this.getProjectPowerInfo();
    if(this.$route.query.page) {
    		this.page = this.$route.query.page;
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    projectManagerId(val, val1) {
      if (val != '' && val != undefined && val != null) {
        this.peoplelist.forEach(element => {
          if (element.id == val) {} else {
            if (element.children != undefined) {
              element.children.forEach(item => {
                if (item.id == val) {

                  this.$refs.tree.setChecked(val, true);
                  this.$refs.tree.setChecked(val, false)
                  item.disabled = true;

                } else {
                  item.disabled = false;
                  if (item.id == val1) {
                    this.$refs.tree.setChecked(val1, true);
                    this.$refs.tree.setChecked(val1, false)
                  }
                }
              });
            }
          }
        });
        if (this.number == 0) {
          this.number = 1;
        } else {
          let item2 = this.$refs.tree.getCheckedNodes();
          let newArr = [];
          item2.forEach(element => {
            let newObj = {};
            newObj.user_id = element.id;
            newObj.name = element.name
            newArr.push(newObj);
          });
          newArr = newArr.sort(this.compare('user_id'));
          this.otheruser = newArr;
        }
      }
    }
  },
  computed: {

  },
  methods: {
    //获取项目权限信息
    getProjectPowerInfo() {
      getFundUser({
        fund_id: this.fundId
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.peoplelist = data.depart;
          data.depart.forEach((element, index) => {
            if (element.children == undefined || element.children.length == 0) {
              data.depart.splice(index, 1);
            }
          });
          this.projectManagerId = data.manager.user_id;

          data.otheruser = data.otheruser.sort(this.compare('user_id'));
          this.otheruser = data.otheruser;
          this.otheruser.forEach(item => {
            this.defaultChechedArr.push(item.user_id);
          });
        }
      })
    },
    //搜索内容
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    //checkMethod
    checkMethod(item1, item2) {
      let newArr = [];
      item2.checkedNodes.forEach(element => {
        if (element.children == undefined) {
          let newObj = {};
          newObj.user_id = element.id;
          newObj.name = element.name
          newArr.push(newObj);
        }
      });
      newArr = newArr.sort(this.compare('user_id'));
      this.otheruser = newArr;
    },
    //删除人员
    deleteUser(userId) {
      this.otheruser.forEach((element, index) => {
        if (userId == element.user_id) {
          this.otheruser.splice(index, 1);
        }
      });
      let newArr = [];
      this.otheruser.forEach((element, index) => {
        newArr.push(element.user_id);
      });
      this.$refs.tree.setCheckedKeys(newArr);
    },

    //提交信息
    toSetComManagerAndUser() {
      let userIdList = [];
      this.otheruser.forEach(item => {
        userIdList.push(item.user_id);
      });

      if (this.projectManagerId == undefined || this.projectManagerId == '') {
        dialogConfirm('info', '请选择投资经理');
        return false;
      }
      setFundUser({
        fund_id: this.fundId,
        manager_id: this.projectManagerId,
        user_id: userIdList.join(',')
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '基金管理人分配成功');
          this.$router.push({
            name: 'fundPermissions',
            query:{
              page:this.page
            }
          })
        } else {
          dialogConfirm('info', res.data.message);
        }
      })
    },
    compare(property) {
      return function(a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value1 - value2;
      }
    }
  }
};

</script>
<style lang="scss" scoped>
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 36px 40px;
  position: relative;
}

.account-group {
  position: absolute;
  top: 84px;
  bottom: 30px;
  left: 40px;
  right: 40px;
  overflow: auto; //width: 100%;
  //max-width: 1160px;
  border: 1px solid #E4E4E4;
  padding: 30px 16px;
}

.company-name {
  font-size: 24px;
  color: #35383A;
  margin-bottom: 15px;
  font-weight: 500;
}

.step-title {
  font-size: 18px;
  color: #3C98E8;
  margin-bottom: 5px;
  font-weight: 500;
}

.step1-box {
  width: 305px;
}

.step1-select-box {
  line-height: 30px;
  height: 30px;
  margin-top: 10px;
  margin-bottom: 23px;
  font-size: 16px;
  font-weight: bold;
  color: #35383A;
  .header-right-time {
    width: 305px;
  }
}

.line {
  width: 100%;
  border-top: 1px solid #F4F4F4;
  margin-top: 25px;
  margin-bottom: 15px;
} //左边
.projectTransfer-left-left {
  width: 305px;
  height: 100%; // padding: 30px 0px 30px 26px;
  .header-right-time {
    width: 100%
  }
}

.projectTransfer-left-left-title {
  font-size: 18px;
  line-height: 1em;
  color: #3C98E8;
  font-weight: bold;
  margin-bottom: 16px;
}

.projectTransfer-left-left-treetitle {
  font-size: 14px;
  color: #A0A4AA;
  line-height: 1em;
  margin-top: 20px;
  margin-bottom: 10px;
  padding-left: 10px;
}

.projectTransfer-left-right {
  width: calc(100% - 405px);
  min-height: 100px;
  padding: 34px 20px 0px;
}

.projectTransfer-left-right-content-title {
  font-size: 14px;
  color: #35383A;
  line-height: 1em;
  margin-bottom: 6px;
}

.select-tree {
  padding-right: 20px;
  border-right: 1px solid #F4F4F4; //树形结构标题盒子
  .select-tree-list {
    max-height: 400px;
    overflow: auto;
  }
  .select-tree-level-title {
    display: flex;
    height: 28px;
    padding-right: 10px;
    align-items: center;
    cursor: pointer;
    &:hover {
      background: #F6FBFF;
    }
    .icon-list-close {
      width: 12px;
      height: 12px;
      display: block;
      margin-right: 6px;
      color: #3C98E8;
      position: relative;
    }
    .icon-list-close:after {
      display: block;
      content: '';
      width: 8px;
      height: 8px;
      background: url('../../images/treelist_listclose.png') no-repeat;
      transform-origin: left bottom;
      position: absolute;
      transition: all 0.3s;
      bottom: 2px;
      left: 2px;
    }
    .icon-list-open:after {
      transform: rotate(-45deg);
      bottom: 0px;
    }
    .select-tree-checkbox {
      margin-right: 8px;
    }
    .select-tree-level2-title-headerimg {
      width: 20px;
      height: 20px; //background: #000;
      border-radius: 50%;
      margin-right: 8px;
    }
  } //树形结构标题
  .select-tree-name {
    font-size: 14px;
    color: #35383A;
    flex-grow: 1;
    overflow: hidden;
    &>span {
      color: #35383A;
      font-size: 13px;
      line-height: 1em;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .select-tree-level-box {
    transition: all 0.6s;
    overflow: hidden;
  }
  .select-tree-level-box-close {
    max-height: 0px!important;
  }
  .select-tree-level1>.select-tree-level-title>.select-tree-name>span {
    font-size: 15px;
  } //树形结构二级
  .select-tree-level2 {
    overflow: hidden;
    .select-tree-level-title {
      padding-left: 15px;
    }
  } //树形结构三级
  .select-tree-level3 {
    .select-tree-level-title {
      padding-left: 35px
    }
  }
}

.selected-project-list {
  width: 100%;
  max-height: 444px;
  overflow: auto;
  .selected-project-item {
    width: 100%;
    font-size: 14px; //color: #35383A;
    line-height: 28px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 10px;
    .icon-close {
      width: 12px;
      height: 12px;
      display: block;
      float: right;
      margin-top: 8px;
      margin-right: 10px;
      cursor: pointer;
      visibility: hidden;
      background: url('../../images/projectTransfer_icon.png');
    }
    &:hover {
      background: #F6FBFF;
      .icon-close {
        visibility: visible;
      }
    }
  }
}

.projectTransfer-left-bottom-btnbox {
  margin-top: 50px;
  padding: 10px;
  .save-vicarious-btn {
    line-height: 40px;
    width: 140px;
    text-align: center;
    cursor: pointer;
  }
}

.search-inputbox {
  width: 250px;
  position: relative;
  &:hover {
    .el-icon-circle-close {
      display: block;
    }
  }
  input {
    background: none;
    width: 100%;
    border-radius: 0px;
    background: #F4F4F4;
    border: 1px solid #EAEAEA;
    cursor: pointer;
    height: 30px;
    color: #35383A;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 14px;
  }
  input:focus {
    border-color: #3C98E8;
    color: #35383A;
    background-color: #ffffff;
    box-shadow: 0px 0px 2px 0px #3c98e8;
    &+.el-icon-circle-close {
      display: block;
    }
  }
  .el-icon-circle-close {
    position: absolute;
    right: 8px;
    top: 0px;
    color: #c0c4cc;
    font-size: 14px;
    line-height: 30px;
    cursor: pointer;
    display: none;
  }
  .el-icon-search {
    position: absolute;
    left: 0px;
    top: 0px;
    color: #c0c4cc;
    font-size: 14px;
    line-height: 30px;
  }
}

.nodata {
  font-size: 14px;
  color: #c0c4cc;
  text-align: center;
  i {
    color: #ddd;
    margin-right: 10px;
  }
}

.table-group-title {
  margin-top: 25px;
}

</style>



